  box-sizing: border-box;
  position: relative;
  width: 100%;
  font-size: 18px;
  font-weight: 700;
}

#hello ul {
  box-sizing: border-box;
  position: relative;
  margin-bottom: 30px;
  display: block;
  width: 100%;
}

.box {
  box-sizing: border-box;
  position: relative;
  float: left;
  width: 200px;
  height: 50px;
  margin: 10px 10px;
  text-align: center;
  border-radius: 5px;
  background-color: lightgray;
  border: 2px solid transparent;
  list-style: none;
  background-size: cover;
  background-repeat: no-repeat;
}

.box:hover {
  cursor: pointer;
  border: 2px solid red;
}

.box::tooltip {
  font-size: 20px;
}

.box span {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.preview {
  z-index: 1;
  position: absolute;
  top: 100%;
  left: 0;
  width: 200px;
